<template>
    <div class="fans">
        <div class="header">
            <span @click="back" class="back">
                <img src="../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="headTitle">关注</span>
            <span class="back"></span>
        </div>
        <div class="taijituan toTop">
          <div class="top">
            <span style="width: 2.2rem;">哼哈团</span>
            <span @click="showAll(person)" v-show="person.infors.length > 2">{{ person.word }}</span>
          </div>
          <ul class="personOfConcern">
            <li class="concern" v-for="(infor, index) in person.infors" :key="index" v-show="index < person.num" @click="showDetails(person, index)">
              <span class="concernLeft">
                <span class="concernHead"><img :src="infor.interestPhoto"></span>
                <span class="concernName">{{ infor.interestNickName }}</span>
              </span>
              <span class="forward"><img src="../assets/img/back.png"></span>
            </li>
          </ul>
        </div>
        <div class="gym">
          <div class="top">
            <span>拳馆</span>
            <span @click="showAll(gyms)" v-show="gyms.infors.length > 2">{{ gyms.word }}</span>
          </div>
          <ul class="personOfConcern">
            <li class="concern" v-for="(infor, index) in gyms.infors" :key="index" v-show="index < gyms.num" @click="showDetails(gyms, index)">
              <span class="concernLeft">
                <span class="concernHead"><img :src="infor.interestPhoto"></span>
                <span class="concernName">{{ infor.interestNickName }}</span>
              </span>
              <span class="forward"><img src="../assets/img/back.png"></span>
            </li>
          </ul>
        </div>
        <div class="coach">
          <div class="top">
            <span>教练</span>
            <span @click="showAll(coaches)" v-show="coaches.infors.length > 2">{{ coaches.word }}</span>
          </div>
          <ul class="personOfConcern">
            <li class="concern" v-for="(infor, index) in coaches.infors" :key="index" v-show="index < coaches.num" @click="showDetails(coaches, index)">
              <span class="concernLeft">
                <span class="concernHead"><img :src="infor.interestPhoto"></span>
                <span class="concernName">{{ infor.interestNickName }}</span>
              </span>
              <span class="forward"><img src="../assets/img/back.png"></span>
            </li>
          </ul>
        </div>
        <div class="student">
          <div class="top">
            <span>学员</span>
            <span @click="showAll(students)" v-show="students.infors.length > 2">{{ students.word }}</span>
          </div>
          <ul class="personOfConcern">
            <li class="concern" v-for="(infor, index) in students.infors" :key="index" v-show="index < students.num" @click="showDetails(students, index)">
              <span class="concernLeft">
                <span class="concernHead"><img :src="infor.interestPhoto"></span>
                <span class="concernName">{{ infor.interestNickName }}</span>
              </span>
              <span class="forward"><img src="../assets/img/back.png"></span>
            </li>
          </ul>
        </div>
    </div>
</template>

<script>
import { getConcernList } from '../api/getdata'
import store from '../vuex/store'
export default {
  name: 'concerned',
  data () {
    return {
      infoList: [],
      person: {
        num: 2,
        word: '显示全部',
        showAll: false,
        infors: []
      },
      gyms: {
        num: 2,
        word: '显示全部',
        showAll: false,
        infors: []
      },
      coaches: {
        num: 2,
        word: '显示全部',
        showAll: false,
        infors: []
      },
      students: {
        num: 2,
        word: '显示全部',
        showAll: false,
        infors: []
      }
    }
  },
  mounted () {
    this.getConcernList()
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    getConcernList () {
      let data = getConcernList({fUserId: store.state.userId})
      data.then(res => {
        this.infoList = res.data
        // console.log(res.data)
        for (var i = 0; i < res.data.length; i++) {
          if (res.data[i].interestType === 'M') {
            this.students.infors.push(res.data[i])
          } else if (res.data[i].interestType === 'T') {
            this.coaches.infors.push(res.data[i])
          } else if (res.data[i].interestType === 'C') {
            this.gyms.infors.push(res.data[i])
          } else {
            this.person.infors.push(res.data[i])
          }
        }
      })
    },
    showDetails (items, index) {
      // console.log(index)
      // console.log(this.students[index])
      store.commit('getUserId', items.infors[index].fUserId)
      store.commit('getUserType', items.infors[index].fansType)
      this.$router.push('/home/community/users')
    },
    showAll (obj) {
      // console.log(obj)
      obj.showAll = !obj.showAll
      obj.num = obj.showAll ? 2 : obj.infors.length
      obj.word = obj.showAll ? '显示全部' : '收起'
    }
  }
}
</script>

<style scoped>
  * {
    font-size: 0.5rem;
    margin: 0;
    padding: 0;
  }

  .users {
    width: 100%;
  }

  .header {
    width: 100%;
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: fixed;
    top: 0;
    left: 0;
    background: #fff;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  .back img {
    width: 100%;
  }

  .top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 0.8rem;
    border-top-right-radius: .4rem;
    border-bottom-right-radius: .4rem;
  }

  .top span {
    vertical-align: middle;
  }

  .top span:first-child {
    color: #fff;
    width: 1.8rem;
    background: #ffd200;
    border-top-right-radius: .4rem;
    border-bottom-right-radius: .4rem;
  }

  .top span:last-child {
    color: #666666;
    padding-right: .4rem;
    width: 2.2rem;
    text-align: end;
  }

  .personOfConcern {
    border-bottom: 3px solid #f5f5f5;
    list-style: none;
  }

  .concernLeft {
    height: 2.026667rem;
    display: flex;
    align-items: center;
  }

  .concernName {
    margin-left: .4rem;
  }
  .concernHead {
    width: 1.28rem;
    height: 1.28rem;
    border-radius: 0.64rem;
    overflow: hidden;
  }

  .concernHead img {
    width: 100%;
    height: 100%;
  }

  .concern {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 2.026667rem;
    margin: 0 .4rem;
    border-bottom: 1px solid #f5f5f5;
  }

  .forward {
    height: 0.5rem;
    width: 0.266667rem;
  }

  .forward img{
    width: 100%;
    height: 100%;
    transform: rotate( 180deg )
  }

  .taijituan.toTop {
    margin-top: 1.9rem;
  }

  .taijituan,
  .gym,
  .coach,
  .student {
    margin-top: .4rem;
  }
</style>



